import VueRouter from 'vue-router';

export function makeNunurVueRouter(nunurVueComponentTable) {
    return new VueRouter({
        routes: [{
            path: '/',
            component: nunurVueComponentTable.mainSection
        }, {
            path: '/manage',
            component: nunurVueComponentTable.manageSection
        }, {
            path: '/:nunurAddress',
            component: nunurVueComponentTable.profileSection
        }]
    });
}