import Vue from 'vue';

import NunurClient from './nunurClient';

import {nunurVueComponentTable} from './nunurVueComponentTable';

import {makeNunurVueRouter} from './makeNunurVueRouter';
import {makeNunurVuexStore} from './makeNunurVuexStore';

console.log(nunurVueComponentTable.nunur);

const nunurClient = new NunurClient();

const el = '#app';
const components = {nunur: nunurVueComponentTable.nunur};
const router = makeNunurVueRouter(nunurVueComponentTable);
const store = makeNunurVuexStore(Vue, nunurClient);

const vue = new Vue({el, components, router, store});
store.dispatch('initialize');
